import {
	mapMutations
} from 'vuex'
export default {
	name: 'my',
	data() {
		return {



			//
			// isH5Plus:false,
			//
			userinfo: {},
			loginShow: true,
			orderTypeLise: [
				//name-标题 icon-图标 badge-角标
				{
					name: '全部订单',
					icon: 'l1.png',
					badge: 1
				},
				{
					name: '待支付',
					icon: 'l2.png',
					badge: 2
				},
				// {name:'待收货',icon:'l3.png',badge:6},
				{
					name: '待评价',
					icon: 'l4.png',
					badge: 9
				}
				// {name:'退换货',icon:'l5.png',badge:0}
			],
			severList: [
				[{
						name: '我的收藏',
						icon: 'point.png'
					},
					{
						name: '优惠券',
						icon: 'quan.png'
					},
					{
						name: '红包',
						icon: 'momey.png'
					},
					{
						name: '成为房东',
						icon: 'renw.png'
					},
				],
				[{
						name: '积分明细',
						icon: 'mingxi.png'
					},
					{
						name: '抽奖',
						icon: 'choujiang.png'
					},
					{
						name: '收货地址',
						icon: 'addr.png'
					},
					{
						name: '银行卡',
						icon: 'bank.png'
					},
					{
						name: '安全中心',
						icon: 'security.png'
					},
					{
						name: '在线客服',
						icon: 'kefu.png'
					}
				]
			],
		};
	},
	onShow() {
		this.loginShow = this.$store.state.hasLogin
		// console.log(this.$store.state.hasLogin)
		this.init()
		if (this.$store.state.hasLogin == false) {
			uni.navigateTo({
				url: '../login/login'
			})
		}
	},
	onLoad() {},
	methods: {
		...mapMutations(['logout']),
		init() {
			this.userinfo = this.$store.state.userInfo
		},
		//用户点击订单类型
		toOrderType(index) {
			uni.showToast({
				title: this.orderTypeLise[index].name
			});
		},
		//用户点击列表项
		toPage(list_i, li_i) {
			if (this.severList[list_i][li_i].name == '成为房东') {
				uni.navigateTo({
					url: '../beingCustodian/beingCustodian'
				})
			} else {
				uni.showToast({
					title: this.severList[list_i][li_i].name
				});
			}


		},
		Tologin() {
			uni.navigateTo({
				url: '../login/login'
			})
		},
		//退出登录
		loginOut() {
			var _self = this
			console.log('退出登录')
			this.$store.state.hasLogin = false
			this.$store.state.userInfo = {}
			this.userinfo = {},
				this.loginShow = false
			uni.removeStorage({
				key: 'userInfo'
			})
			
			// uni.showLoading({
			// 	title: '退出登录..',
			// 	mask: false
			// });






















		}
	}
}
